.chatheader{
    display: flex;
 justify-content: space-between;
 align-items: center;
 color: gray;
 padding: 10px;   
}

.chatheader__hash{
    color: gray;
    font-size: 30px;
    padding: 10px;
}
.chatheader__left > h3{
display: flex;
align-items: center;
color: var(--clr-font);
transition: all ease-in-out 0.3s;
}
.chatheader__right{
display: flex;
align-items: center;
flex: 0.5;
justify-content: space-between;

}
.chatheader__right  > .MuiSvgIcon-root{
    padding: 5px;
    cursor: pointer;
    transition: ease-in-out 0.1s;
    }
    .chatheader__right  > .MuiSvgIcon-root:hover{
        color: white;
       
    }

    .chatheader__search{
        display: flex;
        align-items: center;
        color: gray;
        background-color: #2f3135;
        border-radius: 3px;
        padding: 3px;
    }
    .chatheader__search > input{
        background: transparent;
        outline-width:0 ;
        color: white;
        border: none;
    }

